.popup {
  position: fixed;
  z-index: 30;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.8);
  opacity: 0;
  visibility: hidden;
  overflow-x: auto;
  transition: all 0.4s;
}
.popup.open {
  opacity: 1;
  visibility: visible;
}
.popup.open .popup__content {
  opacity: 1;
  transform: perspective(600px) translate(0px, 0px) rotate(0deg);
}
.popup__body {
  min-height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 30px;
}
.popup__content {
  position: relative;
  background: #ffffff;
  padding: 56px 95px;
  width: 920px;
  max-height: 300px;
  transition: all 0.4s;
  opacity: 0;
  transform: perspective(600px) translate(0px, -100%) rotate(45deg);
  overflow: auto;
}
@media (max-width: 992px) {
  .popup__content {
    padding: 30px;
  }
}
.popup__close {
  cursor: pointer;
  position: absolute;
  top: 40px;
  right: 40px;
}
@media (max-width: 992px) {
  .popup__close {
    top: 20px;
    right: 20px;
  }
}
.popup__title {
  font-weight: 700;
  font-size: 42px;
  margin: 0px 0px 24px 0px;
}
@media (max-width: 992px) {
  .popup__title {
    font-size: 30px;
  }
}
.popup__text {
  font-weight: 300;
}
.popup__text--counter {
  font-weight: 700;
}
.popup__reset {
  position: relative;
  display: none;
  background: #000000;
  color: #ffffff;
  border: none;
  cursor: pointer;
  padding: 5px 10px;
  border-radius: 3px;
}
.popup__reset.visible {
  display: block;
}
.popup__info {
  display: none;
  margin: 10px 0px 0px 0px;
  color: #ff0000;
}
.popup__info.visible {
  display: block;
}